<style>
    #edui429_iframe  .wrapper{
        font-size: 12px!important;
    }
    .preview{
        min-height: 200px;
        width: 375px;

    }
    .editorBox{
        margin: 10px 40px;
    }
    /*img{*/
    /*width: 100%;*/
    /*height: auto*/
    /*}*/
</style>
<template>
    <div class="editorBox">
        <div class="preview" v-html="msg"></div>
        <vue-ueditor-wrap v-model="msg" :config="myConfig" @beforeInit="addCustomButtom" :key="1"></vue-ueditor-wrap>
        <div @click="save()"></div>
    </div>
</template>

<script>
    import '../../static/UEditor/ueditor.config.js'
    import '../../static/UEditor/ueditor.all.js'
    import '../../static/UEditor/lang/zh-cn/zh-cn.js'
    // import VueUeditorWrap from './components/vue-ueditor-wrap.vue';
    // vue-ueditor-wrap.js
    // import VueUeditorWrap from '../lib/vue-ueditor-wrap.min.js'; // ES6 Module
    // const VueUeditorWrap = require('../lib/vue-ueditor-wrap.min.js'); // CommonJS
    // const VueUeditorWrap = window.VueUeditorWrap; //  script 
    export default {
        components: {
            // VueUeditorWrap
        },
        data () {
            return {
                msgData: '',
                msg: '',
                myConfig: {
                    autoHeightEnabled: false,
                    initialFrameHeight: 200,
                    initialFrameWidth: '50%',
                    UEDITOR_HOME_URL: 'static/UEditor/',
                }
            };
        },
        created(){

            var imgs = document.getElementsByTagName('img');

            for(var i in imgs)
            {
                var img = imgs[i];
                img.style.width='100%';
                img.style.height='auto';
            }

        },
        watch:{
            'msg':function(){
                console.log(this.msg)
            }
        },
        methods: {
            save(){
                this.msgData =   this.c
            },
            // 
            addCustomButtom (editorId) {
                window.UE.registerUI('test-button', function (editor, uiName) {
                    //  command 
                    editor.registerCommand(uiName, {
                        execCommand: function () {
                            editor.execCommand('inserthtml', `<span></span>`);
                        }
                    });
                    //  button
                    var btn = new window.UE.ui.Button({
                        // 
                        name: uiName,
                        // 
                        title: '',
                        //  icon  2
                        cssRules: "background-image: url('/test-button.png') !important;background-size: cover;",
                        // 
                        onclick: function () {
                            // 
                            editor.execCommand(uiName);
                        }
                    });
                    // 
                    editor.addListener('selectionchange', function () {
                        var state = editor.queryCommandState(uiName);
                        if (state === -1) {
                            btn.setDisabled(true);
                            btn.setChecked(false);
                        } else {
                            btn.setDisabled(false);
                            btn.setChecked(state);
                        }
                    });
                    //  button button
                    return btn;
                }, 0 /*  */, editorId /*  UI  */);
            },
            // 
            addCustomDialog (editorId) {
                window.UE.registerUI('test-dialog', function (editor, uiName) {
                    //  dialog
                    var dialog = new window.UE.ui.Dialog({
                        //  2
                        iframeUrl: '/customizeDialogPage.html',
                        // 
                        editor: editor,
                        //  dialog 
                        name: uiName,
                        // dialog 
                        title: ' Dialog ',
                        //  dialog 
                        cssRules: 'width:600px;height:300px;',
                        //  buttons  dialog 
                        buttons: [
                            {
                                className: 'edui-okbutton',
                                label: '',
                                onclick: function () {
                                    dialog.close(true);
                                }
                            },
                            {
                                className: 'edui-cancelbutton',
                                label: '',
                                onclick: function () {
                                    dialog.close(false);
                                }
                            }
                        ]
                    });
                    // 
                    var btn = new window.UE.ui.Button({
                        name: 'dialog-button',
                        title: '',
                        cssRules: `background-image: url('/test-dialog.png') !important;background-size: cover;`,
                        onclick: function () {
                            // dialog
                            dialog.render();
                            dialog.open();
                        }
                    });
                    return btn;
                }, 0 /*  */, editorId /* UI */);
            }
        }
    };
</script>



