import Vue from 'vue'
import Router from 'vue-router'
import salesspecification from '@/views/commodity/salesspecification'//
import commoditymanage from '@/views/commodity/commoditymanage'//
import editorcommondity from '@/views/commodity/editorcommondity'//
import commoditydetail from '@/views/commodity/commoditydetail'//
import accountpermission from '@/views/account/accountpermission'//
import adressset from '@/views/baseset/adressset'//
import baseInfo from '@/views/baseset/baseInfo'//
import baseinfoDetail from '@/views/baseset/baseinfoDetail'//


import questionmanage  from '@/views/helpcenter/questionmanage'//
import customerlist from '@/views/customer/customerlist'//
import customerdetail from '@/views/customer/customerdetail'///
import orderlist from '@/views/order/orderlist'//
import orderDetail from '@/views/order/orderDetail' //
import revenuequery from '@/views/order/revenuequery'//
import integralmanage from '@/views/integralmanage/integralmanage'//
import integraldetail from '@/views/integralmanage/integraldetail'//
import limitactiveList from '@/views/marketing/limitactiveList'//
import addlimitactive from '@/views/marketing/addlimitactive'//
import clockdaybackground from '@/views/templatemanage/clockdaybackground'//
import clocknightbackground from '@/views/templatemanage/clocknightbackground'//
import posterbackground from '@/views/templatemanage/posterbackground'//

import activityposter from '@/views/templatemanage/activityposter'//
import guidebackground from '@/views/templatemanage/guidebackground'//


import dynamicmanage from '@/views/communitymanage/dynamicmanage'//
import topicmanage from '@/views/communitymanage/topicmanage'//
import publisherlist from '@/views/communitymanage/publisherlist'//
import visitlist from '@/views/customer/visitlist'//
import dynamicDetail from '@/views/communitymanage/dynamicDetail'//
import insurancelist from '@/views/insurance/insurancelist'//
import addinsurance from '@/views/insurance/addinsurance'//
import insuranceOrderlist from '@/views/order/insuranceOrderlist'//
import insuranceOrderdetail from '@/views/order/insuranceOrderdetail'//

import home from '@/views/home/home'
import login from '@/views/login'
import vueEditor from '@/views/vueEditor'
Vue.use(Router);;
export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/order/insuranceOrderlist',
            name: 'insuranceOrderlist',
            component: insuranceOrderlist
        },
        {
            path: '/order/insuranceOrderdetail',
            name: 'insuranceOrderdetail',
            component: insuranceOrderdetail
        },
        {
            path: '/insurance/insurancelist',
            name: 'insurancelist',
            component: insurancelist
        },
        {
            path: '/insurance/addinsurance',
            name: 'addinsurance',
            component: addinsurance
        },
        {
            path: '/home/home',
            name: 'home',
            component: home
        },
        {
            path: '/templatemanage/activityposter',
            name: 'activityposter',
            component: activityposter
        },
        
        {
            path: '/customer/visitlist',
            name: 'visitlist',
            component: visitlist
        },
        {
            path: '/templatemanage/guidebackground',
            name: 'guidebackground',
            component: guidebackground
        },
        {
            path: '/order/revenuequery',
            name: 'revenuequery',
            component: revenuequery
        },
        {
            path: '/communitymanage/dynamicmanage',
            name: 'dynamicmanage',
            component: dynamicmanage
        },
        {
            path: '/communitymanage/dynamicDetail',
            name: 'dynamicDetail',
            component: dynamicDetail
        },
        {
            path: '/customer/visitlist',
            name: 'visitlist',
            component: visitlist
        },
        {
            path: '/communitymanage/topicmanage',
            name: 'topicmanage',
            component: topicmanage
        },
        {
            path: '/communitymanage/publisherlist',
            name: 'publisherlist',
            component: publisherlist
        },
        {
            path: '/commodity/salesspecification',
            name: 'salesspecification',
            component: salesspecification
        },
        
        {
            path: '/templatemanage/posterbackground',
            name: 'posterbackground',
            component: posterbackground
        },
        {
            path: '/templatemanage/clockdaybackground',
            name: 'clockdaybackground',
            component: clockdaybackground
        },
        {
            path: '/templatemanage/clocknightbackground',
            name: 'clocknightbackground',
            component: clocknightbackground
        },
        {
            path: '/integralmanage/integralmanage',
            name: 'integralmanage',
            component: integralmanage
        },
        {
            path: '/integralmanage/integraldetail',
            name: 'integraldetail',
            component: integraldetail
        },
        {
            path: '/marketing/limitactiveList',
            name: 'limitactiveList',
            component: limitactiveList
        }, {
            path: '/marketing/addlimitactive',
            name: 'addlimitactive',
            component: addlimitactive
        },
        {
            path: '/commodity/commoditymanage',
            name: 'commoditymanage',
            component: commoditymanage
        },
        {
            path:'/commodity/editorcommondity',
            name: 'editorcommondity',
            component: editorcommondity
        },
        {
            path:'/commodity/commoditydetail',
            name: 'commoditydetail',
            component: commoditydetail
        },
        {
            path:'/baseset/baseinfoDetail',
            name: 'baseinfoDetail',
            component: baseinfoDetail
        },
        {
            path:'/baseset/baseInfo',
            name: 'baseInfo',
            component: baseInfo
        },
        {
            path:'/baseset/adressset',
            name: 'adressset',
            component: adressset
        },
        {
            path:'/helpcenter/questionmanage',
            name: 'questionmanage',
            component: questionmanage
        },
        {
            path:'/customer/customerlist',
            name: 'customerlist',
            component: customerlist
        },
        {
            path: '/customer/customerdetail',
            name: 'customerdetail',
            component: customerdetail
        },
        {
            path:'/order/orderlist',
            name: 'orderlist',
            component: orderlist
        },
        {
            path:'/order/orderDetail',
            name: 'orderDetail',
            component: orderDetail
        },
        {
            path:'/account/accountpermission',
            name: 'accountpermission',
            component: accountpermission
        },
      
      
        {
            path: '/',
            name: 'login',
            component: login
        },
        {
            path:'/vueEditor',
            name: 'vueEditor',
            component: vueEditor
        }
  ]
})



