<style rel="stylesheet/scss" lang="scss"  scoped>
    .phone{
        border: none;
        border-bottom: 1px solid  #c0ccda;
        background: #fff;
        color: #232323;
    }
    .login{
        background: url(../assets/images/loginBac.jpg) no-repeat center center;
        height: 100vh;
        width: 100%;
        background-size: cover;
        position: relative;
        .loginInfo{
            position: absolute;
            background: #fff;
            .infoBox{
                display: flex;
                .loginLeft{
                    img{
                        width: 129px;
                        height: 129px;
                        display: inline-block;
                        vertical-align: middle;
                    }
                }
                .loginRight{
                    color: #232323;
                    .loginText{
                        font-size: 24px;
                    }
                    .codeBox{
                        position: relative;
                        .code{
                            width: 60%;
                            height: auto!important;
                        }
                        .codeText{
                            position: absolute;
                            font-size: 12px;
                        }
                    }
                    .phone{
                        border: none;
                    }
                    .code{
                        border: none;
                        border-bottom: 1px solid  #c0ccda;
                        background: #fff;
                    }
                }
            }
        }
    }
    @media screen and (min-width: 1600px) {
        .login{
            height: 100vh;
            width: 100%;
            .loginInfo{
                position: absolute;
                width: 892px;
                height: 478px;
                background: #fff;
                left: calc(0.5*(100% - 892px));
                top: calc(0.5*(100vh - 478px));
                .infoBox{
                    padding: 71px 102px;
                    height: 335px;
                    .loginLeft{
                        width: 261px;
                        border-right: 1px solid #c0ccda;
                        line-height: 335px;
                    }
                    .loginRight{
                        margin-left: 100px;
                        .loginText{
                            margin-bottom: 50px;
                        }
                        .codeBox{
                            .code{
                                width: 78%;
                            }
                            .codeText{
                                top:22px;
                                right: 0;
                                border-bottom: 1px solid  #c0ccda;
                                padding-bottom: 22px;
                                .codeSpan{
                                    background: #0d2c4b;
                                    padding: 8px 8px;
                                    color: #fff;
                                }
                                .active {
                                    color: #fff;
                                }
                            }
                        }
                        .phone{
                            padding: 22px 0;
                            width:100%;
                            color: #232323;
                        }
                        .code{
                            padding: 22px 0;
                            width:100%;
                            color: #232323;
                        }
                        .loginButton{
                            margin-top: 50px;
                            width: 312px;
                            height: 50px;
                            line-height: 50px;
                            text-align: center;
                            background: #0d2c4b;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width: 1599px) {
        .login{
            .loginInfo{
                position: absolute;
                width: 522px;
                height: 280px;
                background: #fff;
                left: calc(0.5*(100% - 522px));
                top: calc(0.5*(100vh - 280px));
                .infoBox{
                    padding: 25px 0;
                    .loginLeft{
                        width: 170px;
                        border-right: 1px solid #c0ccda;
                        line-height: 216px;
                        text-align: center;
                        img{
                            width: 103px;
                            height: 103px;
                            display: inline-block;
                            vertical-align: middle;
                        }
                    }
                    .loginRight{
                        margin: 0 auto;
                        width: 180px;
                        .loginText{
                            margin-bottom: 25px;
                        }
                        .codeBox{
                            .codeText{
                                top:16px;
                                right: -4px;
                                border-bottom: 1px solid  #c0ccda;
                                padding-bottom: 16px;
                                .codeSpan{
                                    background: #0d2c4b;
                                    padding: 8px 8px;
                                    color: #fff;
                                }
                            }
                        }
                        .phone{
                            padding: 16px 0;
                            width:100%;
                            color: #232323;
                        }
                        .code{
                            padding: 16px 0;
                            width:100%;
                            color: #232323;
                        }
                        .loginButton{
                            margin-top: 25px;
                            width: 100%;
                            height: 40px;
                            line-height: 40px;
                            text-align: center;
                            background: #0d2c4b;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
</style>
<template>
    <div class="login">
        <div class="loginInfo">
            <div class="infoBox">
                <div class="loginLeft">
                    <img src="https://ifxj.oss-cn-shenzhen.aliyuncs.com/lanmao/img/lanmaologo.png"/>
                </div>
                <div class="loginRight">
                    <h3 class="loginText"></h3>

                    <div class="codeBox">
                        <input class="code" type="tel" style='width: 100%;' v-model='phone'  maxlength="11" placeholder="">
                    </div>
                    <div class="codeBox">
                        <input class='code' v-model='code' type="tel" maxlength="6" placeholder="">

                        <div class="codeText">
                            <span class="codeSpan cursor active" v-if='isCode'>{{getCode}}</span>
                            <span class="codeSpan cursor" v-if='!isCode' @click="getCodes($event)" >{{getCode}}</span>
                        </div>
                    </div>
                    <div class="loginButton cursor" id='submit_btn' @click="submit"></div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import patternRules from '../common/patternRules'
    import Service from '../common/service'
    import Util from '../common/util'
    export default {
        name: "login",
        data() {
            return {
                phone: '',
                code: '',
                isCode: false,
                getCode: "",
                time: 60,
            };
        },
        watch:{
            'phone':function(){
                if(this.phone.length == 11){
                    if(!((patternRules.mobile).test(this.phone))){
                        this.open3("");
                        return false;
                    }
                }
            },
            'code':function(){
            }
        },
        mounted(){
            var that = this;
            document.onkeydown = function (e) {
                if (!e) e = window.event;
                if ((e.keyCode || e.which) == 13) {
                    var obtnLogin = document.getElementById("submit_btn");  //submit_btnID
                    obtnLogin.focus();
                    that.submit();//
                }
            }
        },
        methods:{
            open3(message) {
                this.$message({
                    message: message,
                    type: 'warning'
                });
            },
            setCookie(name, value, day) {
                //0expirescookie
                var expires = day * 1000;
                var date = new Date(+new Date() + expires);
                document.cookie = name + "=" + escape(value) + ";expires=" + date.toUTCString();
            },
            getCookie(name) {
                var arr;
                var reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
                if (arr = document.cookie.match(reg)) {
                    return unescape(arr[2]);
                }
                else {
                    return null;
                }
            },
            //  
            getCodes(el) {
                if (!((patternRules.mobile).test(this.phone))) {
                    this.open3('');
                    return false
                } else {
                    let _that = this;
                    var c = document.cookie.indexOf("cookieTime=");

                    if (c != -1) {
                        this.time = this.getCookie('cookieTime');
                    } else {
                        this.time = '60'
                    }
                    if (this.time >= 0 && this.isCode) {
                        this.time = '60';
                        return false
                    } else {
                        //
                        Service.login().smsCode({
                            phone: this.phone
                        }).then(response => {
                            console.log(response)
                            if(response.errorCode != 0){
                                this.$message.error(response.message);
                                return;
                            }
                            clearInterval(t);       //
                            el.target.innerHTML = _that.time + '';
                            this.isCode = true;
                            var t = setInterval(function () {
                                if (_that.time > 0) {
                                    _that.time--;
                                    _that.setCookie('cookieTime', _that.time, _that.time);
                                    el.target.innerHTML = _that.time + ''
                                }
                                if (_that.time === 0) {
                                    _that.time = 10;
                                    _that.isCode = false;
                                    clearInterval(t);       //
                                    el.target.innerHTML = ''
                                }
                            }, 1000)
                        }, err => {
                        });

                    }
                }
            },
            submit() {

                if (!(patternRules.mobile.test(this.phone))) {
                    this.open3("");
                    return false;
                }else if(this.phone.length == 0){
                    this.open3("");
                    return false;
                }else if(this.code.length != 6){
                    this. open3("");
                    return false;
                } else {
                    Service.login().loginSms({
                        phone: this.phone,
                        code: parseInt(this.code)
                    }).then(response => {
                        console.log(response)
                        if (response.hasOwnProperty('errorCode')) {
                            if (response.errorCode == 0) {
                                Util.localStorageUtil.set('access_token',response.data.token);
                                localStorage.setItem('user',JSON.stringify(response.data));
                                var obj = {
                                    index: 0,
                                    from: 'parent',
                                    childindex: '',
                                    thirdIndex: '',
                                };
                                localStorage.setItem('klparam',JSON.stringify(obj));
                                this.$router.push({name: 'home'})
                            } else {
                                this.open3(response.message)
                            }
                        }
                    }, err => {
                    })
                }
            }
        }
    }
</script>




